/** @format */

import React from "react";

const Stars = (props) => {
    let noofStars = [];

    for (let index = 1; index <= props.num; index++) {
        noofStars.push(index.toString());
    }

    return (
        <>
            {noofStars.map(() => {
                return (
                    <svg className="overview__icon-star">
                        <use xlinkHref="img/sprite.svg#icon-star"></use>
                    </svg>
                );
            })}
        </>
    );
};

export default Stars;
