/** @format */

import React from "react";
import Navitems from "./components/Navitems";

const Sidebar = () => {
    return (
        <>
            <nav className="sidebar">
                <ul className="side-nav">
                    <Navitems text="Hotel" icon="home" active />
                    <Navitems text="Flight" icon="aircraft-take-off" />
                    <Navitems text="Car rental" icon="key" />
                    <Navitems text="Tours" icon="map" />
                </ul>

                <div className="legal">
                    &copy; 2017 by trillo. All rights reserved.
                </div>
            </nav>
        </>
    );
};

export default Sidebar;
